import React, { useEffect, useState } from "react";
import NavbarBack from "../../components/NavbarBack";
import MinhaFoto from "../../assets/images/Minha-foto.jpeg";
import { motion } from "framer-motion";
import "./styles.css";

const About = () => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setIsVisible(true);
    }, 1000); // 5000 milissegundos = 5 segundos
  }, []);

  return (
    <motion.div
      className="about-container container-fluid"
      initial={{ opacity: 0 }}
      animate={{ opacity: isVisible ? 1 : 0 }}
      exit={{ opacity: 0, transition: { duration: 0.3 } }}
      transition={{ duration: 0.3 }}
    >
      <div className="about-content-container ">
        <div className="photo-container">
          <img src={MinhaFoto} alt="Uma foto minha de camisa azul" />
        </div>

        <div>
          <div className="about-me-content-container col">
            <h4>Quem eu sou?</h4>
            <div>
              <p>
                Ol! Me chamo Lara Matos Aguirres, gosto de msica, ces, gatos
                e amo uma boa aventura, no importando a forma que ela tenha.
                Moro numa chcara com meus pais, irmos e um gatinho muito lindo
                que se chamado Sujinho. Sou desenvolvedora fullstack, operando
                dos dois lados da moeda para websites, tornando a vida do
                usurio uma pequena jornada fantstica e segura durante a
                navegao.{" "}
              </p>
              <p>
                Confesso que nunca imaginei que um dia estaria na frente de um
                computador desenvolvendo cdigos em Java ou TypeScript, sempre
                achei que seria da rea de humanas, mais especificamente
                desvendando a mente humana e seus diversos traumas e alegrias.
                E, como a vida tem mania de nos supreender, meu primeiro grande
                susto foi conseguir passar para Cincias Contbeis pelo Enem.{" "}
              </p>
              <p>
                Logo depois, acredito eu que o fator decisivo para em fim virar
                para a rea do Desenvolvimento Web, foi falhar aps algumas
                tentativas com vendas online. No meio tempo entre vendas online
                e a faculdade, tive meu primeiro contato com a programao em um
                estgio em um escritrio de contabilidade, onde as planilhas e
                os macros do google se mostraram um verdadeiro enigma.{" "}
              </p>
              <p>
                Estes dois eventos foram marcantes o suficiente para que eu me
                voltasse aos programas e descobrisse que a lgica por trs de um
                site poderia ser uma jornada to pica -e ligeiramente
                trabalhosa rs - quanto Senhor do Anis, Dangeons and Dragons, ou
                qualquer outra aventura medieval fantstica.{" "}
              </p>
              <p>
                Desde ento, a rea de desenvolvimento tem se mostrado um
                esforo dirio, onde a determinao, a disciplina e a pacincia,
                alm da busca continua por conhecimento, esto sendo testados e
                me fortalecido um pouco mais a cada desafio/batalha vencida.{" "}
              </p>
              <p>
                Acredito eu que a melhor parte de uma boa aventura  a evoluo
                que o personagem apresenta do comeo ao fim. Este  o meu desejo
                mais profundo: evoluir at o meu fim.
              </p>
            </div>
          </div>
          <div className="experience-content-container">
            <h6>EXPERINCIA</h6>
            <p>
              Apesar de ainda no ter uma experincia real no mercado de
              trabalho, desenvolvi projetos pessoais localizados no meu{" "}
              <a href="/#/profile">Portiflio</a>. L voc encontrar uma
              descrio sobre cada um deles e uma breve apresentao sobre o
              funcionamento, bem como as tecnologias que os envolvem.
            </p>
          </div>
          <div className="skills-content-container">
            <h6>SKILLS</h6>
            <p>-Java8, lgica de programao e orientao a objetos</p>
            <p>-Desenvolvimento backend orientado a Testes Automatizados</p>
            <p>-Spring Boot</p>
            <p>-Docker, PostgreSQL e MongoDB</p>
            <p>-JavaScritp e TypeScript</p>
            <p>-React.js</p>
            <p>-Node.js</p>
            <p>-Bootstrap e SASS</p>
            <p>-HTML e CSS</p>
          </div>
          <div>
            <h6>AGRADECIMENTOS</h6>
            <p>
              Esta  uma pequena dedicatria as pessoas que fizeram parte da
              minha trajetria at aqui. Como nenhuma histria  contada sozinha
              e sim por um conjunto de pessoas unidas, quero que essas pessoas
              saibam que foram marcantes na minha jornada e que tero meus
              sinceros e eternos agradecimentos: JURANDI DIAS DE CARVALHO,
              SILVIA MATOS CARVALHO, ALESSANDRO AGUIRRES CORREIA E LAILA DEVAI.
              Tambm agradeo a voc que leu at aqui, que teve a pacincia e a
              curiosidade que me permitiu compartilhar um pouquinho de mim por
              essa pgina.
            </p>
          </div>
        </div>
        <div className="back-nav-bar">
          <NavbarBack />
        </div>
      </div>
    </motion.div>
  );
};

export default About;
